import Select from "react-select"
import i18next from "i18next"
export default function MLSelect2(props) {
  return (
    <Select
      placeholder={i18next.t("Select...")}
      getOptionLabel={option => option.Text}
      getOptionValue={option => option.Value}
      //   isMulti={this.getMulti()}
      //   closeMenuOnSelect={this.getMulti() ? false : true}
      delimiter={","}
      //   options={this.getSourceData()}
      //   value={this.getThisValueSelect2()}
      // menuPortalTarget={
      //   this.state.portaling && document.querySelector("body")
      // }
      // menuIsOpen={true}
      menuPortalTarget={document.querySelector("body")}
      styles={{ menuPortal: base => ({ ...base, zIndex: 1090 }) }}
      {...props}
    />
  )
}
